body, html, #root, .App {
  background-color: #282c34;
  height: 100%;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.flex-align-center {
  align-items: center
}

.flex-align-top {
  align-items: flex-start
}

/* Header|Footer */
header, footer {
  color: white;
  display: flex;
  height: 50px;
  justify-content: space-between
}

.action-container {
  display: flex;
  gap: 8px;
  .action-icon {
    font-size: 24px;
    transition: 700ms;
    cursor: pointer;
    color: #fff!important;
    padding-top: 2px;
    &:hover {
        opacity: 0.8
    }
  }
}

.App-logo, .svg-a {
	height: 40px;
	width: 40px;
	position: relative;
	z-index: 10
}

.App-logo {
  left: -10px;
	top: 6px;
}

.svg-a {
  top: 16px;
  left: 0;
  right: -4px
}

footer a, footer a:visited {
  color: #fff!important
}
/* End Header|Footer */

/* Common */
button {
  border-radius: 4px;
  transition: 700ms;
  border: none;
  padding: 12px;
  font-size: 16px;
  cursor: pointer;
  max-width: 200px;
  &:hover {
    opacity: 0.8;
  }
  &.warning {
    background: #ffc107
  }
  &.primary {
    background: #0d6efd;
    color: #fff
  }
  &.secondary {
    background: #6c757d;
    color: #fff
  }
  & span {
    margin-left: 4px
  }
}

h1 {
  font-size: 36px
}

.DN {
  display: none
}

.main-wrapper {
	max-width: 1000px;
	margin: auto
}

